@import 'ScssHelpers/helpers-all.scss';

.waitlist {
	@include bp($point_2, min) {
		padding-top: 17rem;
		padding-bottom: 8rem;
	}

	@include bp($point_3) {
		padding-top: 4rem;
		padding-bottom: 7.8rem;
	}
}

.waitlist--style_big_mod {
	@include bp($point_2, min) {
		padding-top: 14rem;
		padding-bottom: 14rem;
	}

	@include bp($point_3) {
		padding-top: 14.5rem;
		padding-bottom: 14rem;
	}
}

.waitlist__link {
	display: block;
	width: 100%;
	padding: 0 2rem;

	font-family: $raleway;
	font-weight: 700;

	text-transform: uppercase;
	text-align: center;
	transition: background-color 0.3s ease;

	@include bp($point_2, min) {
		height: 10.9rem;

		line-height: 10.9rem;
	}
	@include bp($point_3) {
		height: 8.6rem;
	}

	&:not(.waitlist--style_big_mod) {
		color: $black;
		box-shadow: 0 0 0 6px $black;

		@include bp($point_2, min) {
			font-size: 5.2rem;
		}
		@include bp($point_3) {
			font-size: 3.2rem;
			line-height: 8.6rem;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
		background-color: $yellow;
	}

	.waitlist--style_big_mod & {
		height: auto;
		padding: 0;

		color: $white;
		transition: color 0.3s ease;
		box-shadow: none;

		@include bp($point_2, min) {
			font-size: 12.2rem;
		}

		@include bp($point_3) {
			font-size: 6.5rem;
			color: $yellow;
			line-height: 1;
		}

		&:hover,
		&:focus {
			background-color: transparent;
			color: $yellow;
		}
	}
}
